import logo from './logo.svg';
import './App.css';
import ThreePointViz from './3D_Components/ThreePointViz';
import HoverInfo from './HoverInfo';
import React from 'react';
import TemporaryMenu from './TemporaryMenu';
import { Button, Icon} from 'semantic-ui-react'
import ClickInfo from './ClickInfo';

function sayHello() {
  alert('You clicked me!');
}
/*


*/

function App() {
  const [counter, setCounter] = React.useState(0)
  const [info, setInfo] = React.useState(null)
  const [info_page, setInfoPage] = React.useState(null)

    return (
    <div className="App">
      <TemporaryMenu></TemporaryMenu>
      <div class = "game_container">
        {info_page && (
          <ClickInfo info = {info_page} setInfoPage = {setInfoPage}></ClickInfo>
        )}
        <div className="vis-container">
          <ThreePointViz counter = {counter} setInfo = {setInfo} setInfoPage= {setInfoPage}/>
        </div>
      </div>
      {info && (
        <HoverInfo info = {info}></HoverInfo>
      )}
      <Button type="button" onClick={()=>setCounter(counter - 1)}>RIGHT!</Button>

      <Button type="button" onClick={()=>setCounter(counter + 1)}>LEFT!</Button>

    </div>
  );
}

export default App;
