import * as React from 'react';
import {Canvas} from 'react-three-fiber';

import { useLoader, useFrame  } from '@react-three/fiber'
import { FBXLoader } from 'three/examples/jsm/loaders/FBXLoader'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'
import ProprietaryCamera from './ProprietaryCamera';
import * as THREE from 'three'
//import Model from './Model';
import Anim from './Biomass_animation';
const ThreePointViz = (props) => {
    let WIDTH = window.innerWidth;
    let HEIGHT = window.innerHeight;

    return (
        <Canvas camera={{ fov: 50, position: [2, 5, -17], rotation: [0, 0,0]}}>
            <ProprietaryCamera counter = {props.counter}></ProprietaryCamera>
            <ambientLight intensity={0.0}/>
            <hemisphereLight
                color="#ffffff"
                skyColor="#ffffbb"
                groundColor="#080820"
                intensity={0.5}
            />
                  <pointLight position={[0, 20, 10]} intensity={0.5} />

            <Anim counter = {props.counter} setInfo = {props.setInfo} setInfoPage = {props.setInfoPage}/>
        </Canvas>
    )
}

export default ThreePointViz;